import { Component, OnInit,Output,EventEmitter } from '@angular/core';
import { PeopleHttpService } from '../services/peopleHttp.service'
import { SearchFilterPipe} from '../filter.pipe';

@Component({
  selector: 'side-bar',
  templateUrl: './sideBar.component.html',
  styleUrls: ['./sideBar.component.scss'],
  providers: [PeopleHttpService]
})
export class SideBarComponent implements OnInit {
  recievedObj:object;
  @Output() onUserSelected: EventEmitter<any> = new EventEmitter<any>();
   user: any;
   dataAvailable:boolean =false;
    searchText: String = '';
    personObj:object;
    constructor( private peopleHttpService:PeopleHttpService) {
      peopleHttpService.GetCall().then (
        i => {
          this.user = i;
          console.log("userr object"+this.user);
          this.sendSelectedObj( this.user.People[0]);
          this.dataAvailable=true;
        }
      );
    }
  
  public sendSelectedObj(personObj){
    this.recievedObj = personObj;
    this.onUserSelected.emit(personObj);
  }
     
   buildSearchData = function(text){
    return text.name;
}

    filterText = function (user, st) {
        if(user){
          //user.forEach(function(value){
            //console.log('+++'+value +'user:'+ user);
            var re = new RegExp(st, 'i');
            return !st || re.test(user.name) ;
            //return value.name.toLowerCase().indexOf(st) != -1
            //console.log('*****'+ value.name.toLowerCase().indexOf(st) != -1)
           // value.rating.toLowerCase().indexOf(st) != -1
         // })
       
        }
    };
    ngOnInit() {
      
  //this.filterText;
  
      
    }

}



