import { Component,Input, OnInit,ViewChild, AfterViewInit } from '@angular/core';
import { PeopleHttpService } from '../services/peopleHttp.service';
import { SideBarComponent } from "../sideBar/sideBar.component";

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css'],
  providers: [PeopleHttpService]
})
export class HomeComponent implements OnInit {
  recievedObj:object;
  @Input('receivedOb') recObj :object;
  constructor( private peopleHttpService:PeopleHttpService) {}
  messages = new Array();

  enterMessage = function() {
    let messageSent = this.message;
    if(messageSent){
      this.messages.push(messageSent);
    }
  }
  ngOnInit() {
    
  }

  ngOnDestroy() {
    this.messages = [];
  }
}



